import TreeUtil from "../../../../utils/treeUtil";
import LogicUtil from "./logicUtil";
import ParamUtil from "./paramUtil";


namespace LogicElementUtil {

    export const addChild = (wrapElement: LogicUtil.WrapElement, node: TreeUtil.ElementNode) => {
        return insertChild(wrapElement, node, node.children.length);
    }
    export const insertChild = (wrapElement: LogicUtil.WrapElement, node: TreeUtil.ElementNode, listPos: number) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListName(wrap.type);
        wrap.data[`${listName}`].splice(listPos, 0, wrapElement);
        const newNode = TreeUtil.createNode(wrapElement, node);
        node.children.splice(listPos, 0, newNode);
        return newNode;
    }

    export const removeChild = (node: TreeUtil.ElementNode, listPos: number, size: number) => {
        const wrap = node.data as LogicUtil.WrapElement;
        const listName = getListName(wrap.type);
        wrap.data[`${listName}`].splice(listPos, size);
        node.children.splice(listPos, size);
    }

    export const isHead = (node: TreeUtil.ElementNode) => {
        if (node.parent == null) return false;
        const ownerChildren = node.parent.children;
        return ownerChildren[0] == node;
    }

    export const isTail = (node: TreeUtil.ElementNode) => {
        if (node.parent == null) return false;
        const ownerChildren = node.parent.children;
        return ownerChildren[ownerChildren.length - 1] == node;
    }

    export const swapOrder = (node: TreeUtil.ElementNode, offset: number) => {
        if (node.parent == null) return;
        const ownerChildren = node.parent.children;
        const wrap = node.parent.data as LogicUtil.WrapElement;
        const listName = getListName(wrap.type);
        const ownDataChildren = wrap.data[`${listName}`] as LogicUtil.WrapElement[];

        // 
        let curNo = -1;
        ownerChildren.some((it, i) => {
            if (it == node) {
                curNo = i;
                return 1;
            }
        });
        if (curNo === -1) return;

        // 
        const swapNode = ownerChildren[curNo];
        ownerChildren[curNo] = ownerChildren[curNo + offset];
        ownerChildren[curNo + offset] = swapNode;
        // 
        const swapData = ownDataChildren[curNo];
        ownDataChildren[curNo] = ownDataChildren[curNo + offset];
        ownDataChildren[curNo + offset] = swapData;
    }

    export const getListName = (type: LogicUtil.NodeType) => {
        switch (type) {
            case 'seed-mng':
            case 'logic-mng': return 'defs';
            case 'seed-item':
            case 'connector':
            case 'inherit-logic':
            case 'logic-item': return 'mngs';
            case 'send-mng':
            case 'behavior-mng':
            case 'visual-mng':
            case 'extend-mng':
            case 'visual-mng': return 'params';
            case 'inherit-mng': return 'inherits';
            case 'argument-mng': return 'args';
            case 'field-mng': return 'fields';
            case 'thumbnail-mng': return 'items';
        }
        return '';
    }

    export const getNamableProps = (type: LogicUtil.NodeType) => {
        switch (type) {
            case 'connector': return 'id';
            case 'logic-item': return 'name';
        }
        return '';
    }


    export const createParamWrap = (type: LogicUtil.NodeType, paramDataList: LogicUtil.NodeParamItem): LogicUtil.WrapElement => {
        return { type: type, data: paramDataList };
    }
    export const createParamWrapList = (type: LogicUtil.NodeType, paramDataList: LogicUtil.NodeParamItem[]): LogicUtil.WrapElement[] => {
        return paramDataList.map(data => {
            return { type: type, data: data };
        });
    }

    export const createVisualParams = (): LogicUtil.WrapElement[] => {
        const type = ParamUtil.VisualParamType;
        return createParamWrapList('param-item', [
            { name: type.RADIUS, formulaItems: ['var'], values: [{ type: 'direct', value: '20' }] },
            { name: type.COLOR_R, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_G, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_B, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
            { name: type.COLOR_A, formulaItems: ['var'], values: [{ type: 'direct', value: '255' }] },
        ]);
    }

    export const createBehaviorParams = (): LogicUtil.WrapElement[] => {
        const list: LogicUtil.NodeParamItem[] = [];
        const type = ParamUtil.BehaviorParamType;
        list.push({
            name: type.LIMIT,
            formulaItems: ['var'], values: [{ type: 'direct', value: '30' }]
        });
        list.push({
            name: type.POS_X,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.POS_Y,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.VEC_X,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        list.push({
            name: type.VEC_Y,
            formulaItems: ['var'], values: [{ type: 'direct', value: '0' }]
        });
        return createParamWrapList('param-item', list);
    }

    export const getDefaultLogicMngs = (): LogicUtil.WrapElement[] => {
        return [
            {
                type: 'visual-mng',
                data: {
                    params: createVisualParams()
                } as LogicUtil.NodeParamManager
            },
            {
                type: 'behavior-mng',
                data: {
                    params: createBehaviorParams()
                } as LogicUtil.NodeParamManager
            }
        ];
    }

    export const getDefaultSeedMngs = (): LogicUtil.WrapElement[] => {
        return [
            {
                type: 'thumbnail-mng',
                data: {
                    items: []
                } as LogicUtil.NodeThumnailManager
            },
            {
                type: 'seed-param-mng',
                data: {
                    params: []
                } as LogicUtil.NodeParamManager
            }
        ];
    }

    export const getInitialData = (): LogicUtil.WrapElement => {
        return {
            type: 'root',
            data: {
                seedMng: {
                    type: 'seed-mng',
                    data: {
                        defs: []
                    } as LogicUtil.NodeSeedManager
                } as LogicUtil.WrapElement,
                logicMng: {
                    type: 'logic-mng',
                    data: {
                        defs: []
                    } as LogicUtil.NodeLogicManager
                } as LogicUtil.WrapElement
            } as LogicUtil.NodeRoot
        }
    }

    export const getSeedNameList = (mng: LogicUtil.NodeSeedManager) => {
        const nameList: string[] = [];
        mng.defs.forEach(seed => {
            if (seed.data != null) {
                nameList.push((seed.data as LogicUtil.NodeSeedItem).name);
            }
        });
        return nameList;
    }

    export const getLogicNameList = (mng: LogicUtil.NodeLogicManager) => {
        const nameList: string[] = [];
        mng.defs.forEach(logic => {
            if (logic.data != null) {
                nameList.push((logic.data as LogicUtil.NodeLogicItem).name);
            }
        });
        return nameList;
    }

    export const getInheritIdList = (mng: LogicUtil.NodeConnectManager) => {
        const idList: string[] = [];
        mng.inherits.forEach(inherit => {
            if (inherit.data != null) {
                idList.push((inherit.data as LogicUtil.NodeInheritConnector).id);
            }
        });
        return idList;
    }

    export const getParamNameList = (curLogicOwner: LogicUtil.NodeLogicItem) => {
        const nameList: string[] = [];

        // 
        ParamUtil.VisualParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.BehaviorParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.SelfParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.ExtendParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.EffectParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.ModulateParams.forEach(name => {
            nameList.push(name);
        });
        ParamUtil.Constants.forEach(name => {
            nameList.push(name);
        });

        // 
        const argMng = curLogicOwner.mngs.find(mng => mng.type === 'argument-mng');
        if (argMng != undefined) {
            const args = (argMng.data as LogicUtil.NodeArgumentManager).args;
            args.forEach(arg => {
                if (arg.data != null) {
                    nameList.push((arg.data as LogicUtil.NodeArgumentItem).name);
                }
            });
        }
        // 
        const fieldMng = curLogicOwner.mngs.find(mng => mng.type === 'field-mng');
        if (fieldMng != undefined) {
            const fields = (fieldMng.data as LogicUtil.NodeFieldManager).fields;
            fields.forEach(field => {
                if (field.data != null) {
                    nameList.push((field.data as LogicUtil.NodeParamItem).name);
                }
            });
        }
        return nameList;
    }

    /**
     * 
     * @param prefix 
     * @param node 
     */
    export const getNumberingOnPrefixName = (prefix: string, node: TreeUtil.ElementNode) => {
        let num = 0;
        // 
        while (true) {
            const check = node.children.find(child => {
                const wrap = child.data as LogicUtil.WrapElement;
                if (wrap.data == null) return false;
                console.log(wrap);
                return wrap.data[getNamableProps(wrap.type)] === `${prefix}${num}`;
            });
            if (check != undefined) num++;
            else break;
        }
        return `${prefix}${num}`;
    }

    const getSendParamList = (sendNode: TreeUtil.ElementNode, rootData: LogicUtil.WrapElement): string[] => {
        const connector = (sendNode.parent?.data as LogicUtil.WrapElement).data as LogicUtil.NodeInheritConnector;
        const isCreate = connector.method === 'create';
        if (!isCreate) {
            const rootNode = rootData.data as LogicUtil.NodeRoot;
            const inheritMng = rootNode.logicMng.data as LogicUtil.NodeLogicManager;
            const dependItem = inheritMng.defs.find(wrap => {
                return (wrap.data as LogicUtil.NodeLogicItem).name === connector.depend;
            });
            if (dependItem != undefined) {
                const logicWrap = dependItem;
                const argWrap = (logicWrap.data as LogicUtil.NodeLogicItem).mngs[0];
                if (argWrap.type === 'argument-mng') {
                    return (argWrap.data as LogicUtil.NodeArgumentManager).args.map(arg => {
                        return (arg.data as LogicUtil.NodeArgumentItem).name;
                    });
                }
            }
        }
        return [];
    }

    export const initSendParams = (sendNode: TreeUtil.ElementNode, rootData: LogicUtil.WrapElement) => {
        const sendParamList = getSendParamList(sendNode, rootData);
        // console.log(sendParamList);
        const wrap = sendNode.data as LogicUtil.WrapElement;
        const params = (wrap.data as LogicUtil.NodeParamManager).params;
        // 
        const curParamList = params.map(param => {
            return (param.data as LogicUtil.NodeParamItem).name;
        });
        sendParamList.forEach(param => {
            if (!curParamList.includes(param)) {
                const paramWrap = LogicElementUtil.createParamWrap('send-item',
                    { name: param, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
                );
                params.push(paramWrap);
                sendNode.children.push(TreeUtil.createNode(paramWrap, sendNode));
            }
        });
    }

    /**
     * 
     * @param logicMngNode 
     */
    export const syncLogicName = (logicMngNode: TreeUtil.ElementNode, prevName: string, newName: string) => {

        const runRec = (logic: LogicUtil.NodeLogicItem) => {
            const inhMng = logic.mngs.find(mng => mng.type === 'inherit-mng');
            if (inhMng == undefined) return;
            (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                const conn = inh.data as LogicUtil.NodeInheritConnector;
                if (conn.method === 'create') {
                    runRec(conn.mngs[2].data);
                } else {
                    if (conn.depend === prevName) {
                        conn.depend = newName;
                    }
                }
            });
        }
        logicMngNode.children.forEach(logic => {
            runRec((logic.data as LogicUtil.WrapElement).data);
        });
    }

    /**
     * 
     * @param logicNode 
     */
    export const syncFieldName = (logicNode: TreeUtil.ElementNode, prevName: string, newName: string) => {

        const syncFormula = (list: LogicUtil.WrapElement[]) => {
            list.forEach(wrap => {
                const values = (wrap.data as LogicUtil.NodeParamItem).values;
                values.forEach(varItem => {
                    if (varItem.type === 'field' && varItem.value === prevName) {
                        varItem.value = newName;
                    }
                });
            });
        }

        const logic = (logicNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeLogicItem;
        const visualMng = logic.mngs.find(mng => mng.type === 'visual-mng');
        if (visualMng != undefined) {
            syncFormula((visualMng.data as LogicUtil.NodeParamManager).params);
        }
        const behaviorMng = logic.mngs.find(mng => mng.type === 'behavior-mng');
        if (behaviorMng != undefined) {
            syncFormula((behaviorMng.data as LogicUtil.NodeParamManager).params);
        }
        const inhMng = logic.mngs.find(mng => mng.type === 'inherit-mng');
        if (inhMng != undefined) {
            (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                const mngs = (inh.data as LogicUtil.NodeInheritConnector).mngs;
                syncFormula((mngs[0].data as LogicUtil.NodeParamManager).params);
                syncFormula((mngs[1].data as LogicUtil.NodeParamManager).params);
            });
        }
    }

    /**
     * 
     * @param logicNode 
     */
    export const syncArgumentName = (logicNode: TreeUtil.ElementNode, prevName: string, newName: string) => {

        const syncFormula = (list: LogicUtil.WrapElement[]) => {
            list.forEach(wrap => {
                const values = (wrap.data as LogicUtil.NodeParamItem).values;
                values.forEach(varItem => {
                    if (varItem.type === 'argument' && varItem.value === prevName) {
                        varItem.value = newName;
                    }
                });
            });
        }

        const wrap = logicNode.data as LogicUtil.WrapElement;
        const logicItem = wrap.data as LogicUtil.NodeLogicItem;
        const fieldMng = logicItem.mngs.find(mng => mng.type === 'field-mng');
        if (fieldMng != undefined) {
            syncFormula((fieldMng.data as LogicUtil.NodeFieldManager).fields);
        }
        const visualMng = logicItem.mngs.find(mng => mng.type === 'visual-mng');
        if (visualMng != undefined) {
            syncFormula((visualMng.data as LogicUtil.NodeParamManager).params);
        }
        const behaviorMng = logicItem.mngs.find(mng => mng.type === 'behavior-mng');
        if (behaviorMng != undefined) {
            syncFormula((behaviorMng.data as LogicUtil.NodeParamManager).params);
        }
        const inhMng = logicItem.mngs.find(mng => mng.type === 'inherit-mng');
        if (inhMng != undefined) {
            (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                const mngs = (inh.data as LogicUtil.NodeInheritConnector).mngs;
                syncFormula((mngs[0].data as LogicUtil.NodeParamManager).params);
                syncFormula((mngs[1].data as LogicUtil.NodeParamManager).params);
            });
        }

        const syncParamName = (params: LogicUtil.WrapElement[]) => {
            params.forEach(wrap => {
                const item = wrap.data as LogicUtil.NodeParamItem;
                if (item.name === prevName) {
                    item.name = newName;
                }
            });
        }
        if (wrap.type === 'logic-item') {

            const runRec = (logic: LogicUtil.NodeLogicItem) => {
                const inhMng = logic.mngs.find(mng => mng.type === 'inherit-mng');
                if (inhMng == undefined) return;
                (inhMng.data as LogicUtil.NodeConnectManager).inherits.forEach(inh => {
                    const conn = inh.data as LogicUtil.NodeInheritConnector;
                    if (conn.method === 'create') {
                        runRec(conn.mngs[2].data);
                    } else if (conn.method === 'depend' && conn.depend === logicItem.name) {
                        const sendMng = conn.mngs[1].data as LogicUtil.NodeParamManager;
                        syncParamName(sendMng.params);
                    }
                });
            }
            const logicMngNode = logicNode.parent as TreeUtil.ElementNode;
            logicMngNode.children.forEach(logic => {
                runRec((logic.data as LogicUtil.WrapElement).data);
            });
        } else if (wrap.type === 'inherit-logic') {
            const conn = (logicNode.parent?.data as LogicUtil.WrapElement).data as LogicUtil.NodeInheritConnector;
            const sendMng = conn.mngs[1].data as LogicUtil.NodeParamManager;
            syncParamName(sendMng.params);
        }
    }


    /**
     * Sendcallback
     * @param logicNode 
     * @param callback 
     */
    const syncArgumentCallback = (
        logicNode: TreeUtil.ElementNode,
        callback: (sendNode: TreeUtil.ElementNode) => void
    ) => {

        const wrap = logicNode.data as LogicUtil.WrapElement;
        const logicItem = wrap.data as LogicUtil.NodeLogicItem;

        if (wrap.type === 'logic-item') {
            const runRec = (logicNode: TreeUtil.ElementNode) => {
                const inhMngNode = logicNode.children.find(child => (child.data as LogicUtil.WrapElement).type === 'inherit-mng');
                if (inhMngNode == undefined) return;
                inhMngNode.children.forEach(child => {
                    const conn = (child.data as LogicUtil.WrapElement).data as LogicUtil.NodeInheritConnector;
                    if (conn.method === 'create') {
                        runRec(child.children[2]);
                    } else if (conn.method === 'depend' && conn.depend === logicItem.name) {
                        callback(child.children[1]);
                    }
                });
            }
            const logicMngNode = logicNode.parent as TreeUtil.ElementNode;
            logicMngNode.children.forEach(child => {
                runRec(child);
            });
        } else if (wrap.type === 'inherit-logic') {
            const sendNode = logicNode.parent?.children[1] as TreeUtil.ElementNode;
            callback(sendNode);
        }
    }

    /**
     * Send
     * @param logicNode 
     */
    export const syncArgumentAdd = (logicNode: TreeUtil.ElementNode, newArgName: string) => {
        const addNewParam = (sendNode: TreeUtil.ElementNode) => {
            const paramWrap = LogicElementUtil.createParamWrap('send-item',
                { name: newArgName, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
            );
            const sendMng = (sendNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;
            sendMng.params.push(paramWrap);
            sendNode.children.push(TreeUtil.createNode(paramWrap, sendNode));
        }
        syncArgumentCallback(logicNode, addNewParam);
    }

    /**
     * Send
     * @param logicNode 
     */
    export const syncArgumentDel = (logicNode: TreeUtil.ElementNode, delArgName: string) => {
        const delArgParam = (sendNode: TreeUtil.ElementNode) => {
            const sendMng = (sendNode.data as LogicUtil.WrapElement).data as LogicUtil.NodeParamManager;
            let delIndex = -1;
            sendMng.params.some((param, i) => {
                if((param.data as LogicUtil.NodeParamItem).name == delArgName) {
                    delIndex = i;
                    return 1;
                }
            });
            sendMng.params.splice(delIndex, 1);
            sendNode.children.splice(delIndex, 1);
        }
        syncArgumentCallback(logicNode, delArgParam);
    }
}

export default LogicElementUtil;