import { useContext, useEffect, useMemo, useState } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import ValidateUtil from "../../../../utils/form/validateUtil";
import TreeUtil from "../../../../utils/treeUtil";
import ConfirmDialog from "../../../dialog/confirmDialog";
import { GlobalContext } from "../../../entry/systemEntry";
import { FactoryTreeContext } from "../logicMainteTab";
import LogicElementUtil from "../util/logicElementUtil";
import LogicUtil from "../util/logicUtil";
import ModifyOperations from "./common/modifyOperations";

const FactDetInheritConnector = () => {
    const { store, setStore } = useContext(GlobalContext);
    const { rootData, node, invalidate, setModify, isModify, removeSelf } = useContext(FactoryTreeContext);
    const wrap = node.data as LogicUtil.WrapElement;

    // const [condition, setCondition] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [identify, setIdentify] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [method, setMethod] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });
    const [depend, setDepend] = useState<FormUtil.CheckableValue>({ value: '', errors: [] });

    const mappingForm = () => {
        const param = wrap.data as LogicUtil.NodeInheritConnector;
        setMethod({ ...method, value: param.method });
        setDepend({ ...depend, value: param.depend });
        setIdentify({ ...identify, value: param.id });
    };

    useEffect(() => {
        if (wrap.data == null) {
            setModify(true);
            // 
            const defaultName = LogicElementUtil.getNumberingOnPrefixName('id_', node.parent as TreeUtil.ElementNode);
            setIdentify({ ...identify, value: defaultName });
        } else {
            mappingForm();
        }
    }, [node]);

    const isCreate = method.value === 'create';

    /**  */
    const logicList = useMemo((): string[] => {
        const rootNode = rootData.data as LogicUtil.NodeRoot;
        const inheritMng = rootNode.logicMng.data as LogicUtil.NodeLogicManager;
        return inheritMng.defs.map(wrap => {
            return (wrap.data as LogicUtil.NodeLogicItem).name;
        });
    }, [node]);
    
    /** ID */
    const idList = useMemo(() => {
        const mng = (node.parent?.data as LogicUtil.WrapElement).data;
        return LogicElementUtil.getInheritIdList(mng);
    }, [node]);

    // const getSendParamList = (): string[] => {
    //     if (!isCreate) {
    //         const rootNode = rootData.data as LogicUtil.NodeRoot;
    //         const inheritMng = rootNode.logicMng.data as LogicUtil.NodeLogicManager;
    //         const dependItem = inheritMng.defs.find(wrap => {
    //             return (wrap.data as LogicUtil.NodeLogicItem).name === depend.value;
    //         });
    //         if (dependItem != undefined) {
    //             const argWrap = (dependItem.data as LogicUtil.NodeLogicItem).mngs[0];
    //             if (argWrap.type === 'argument-mng') {
    //                 return (argWrap.data as LogicUtil.NodeArgumentManager).args.map(arg => {
    //                     return (arg.data as LogicUtil.NodeArgumentItem).name;
    //                 });
    //             }
    //         }
    //     }
    //     return [];
    // }

    // const setSendParams = (param: LogicUtil.NodeInheritConnector) => {

    //     const sendIndex = param.mngs[0].type === 'send-mng' ? 0 : 1;
    //     const sendMng = param.mngs[sendIndex].data as LogicUtil.NodeParamManager;
    //     const sendNode = node.children[sendIndex];
    //     sendMng.params = [];
    //     sendNode.children = [];
    //     getSendParamList().forEach(param => {
    //         const paramWrap = LogicElementUtil.createParamWrap('send-item',
    //             { name: param, formulaItems: ['var'], values: [{ type: 'direct', value: '0' }] }
    //         );
    //         sendMng.params.push(paramWrap);
    //         sendNode.children.push(TreeUtil.createNode(paramWrap, sendNode));
    //     });
    // }

    const initConnectorNode = (param: LogicUtil.NodeInheritConnector) => {
        param.mngs = [];
        node.children = [];
        param.mngs.push({
            type: 'extend-mng',
            data: {
                params: []
            } as LogicUtil.NodeParamManager
        });
        param.mngs.push({
            type: 'send-mng',
            data: {
                params: []
            } as LogicUtil.NodeParamManager
        });
        if (isCreate) {
            param.mngs.push({
                type: 'inherit-logic',
                data: {
                    mngs: LogicElementUtil.getDefaultLogicMngs()
                } as LogicUtil.NodeInheritItem
            });
        }
        wrap.data = param;

        param.mngs.forEach(mng => {
            const newNode = TreeUtil.createNode(mng, node);
            const data = (newNode.data as LogicUtil.WrapElement);
            switch (data.type) {
                case 'inherit-logic': {
                    (data.data as LogicUtil.NodeInheritItem).mngs.forEach(mng => {
                        const inheritNode = TreeUtil.createNode(mng, newNode);
                        newNode.children.push(inheritNode);

                        switch (mng.type) {
                            case 'visual-mng': {
                                (mng.data as LogicUtil.NodeParamManager).params.forEach(param => {
                                    inheritNode.children.push(TreeUtil.createNode(param, inheritNode));
                                });
                            } break;
                            case 'behavior-mng': {
                                (mng.data as LogicUtil.NodeParamManager).params.forEach(param => {
                                    inheritNode.children.push(TreeUtil.createNode(param, inheritNode));
                                });
                            } break;
                        }
                    });
                } break;
                case 'send-mng': {
                    console.log('initSendParams');
                    LogicElementUtil.initSendParams(newNode, rootData);
                } break;
            }
            node.children.push(newNode);
        });
        // setSendParams(param);

    }

    const isDisable = wrap.data == null ? false : (wrap.data as LogicUtil.NodeInheritConnector).disable ?? false;

    return (<>
        <ModifyOperations.Component
            mappingForm={mappingForm}
            checkableFields={[identify, method, depend]}
            firstRegist={() => {
                const param: LogicUtil.NodeInheritConnector = {
                    method: method.value as LogicUtil.InheritMethod,
                    depend: depend.value,
                    id: identify.value,
                    mngs: []
                };
                initConnectorNode(param);
                invalidate();
            }}
            modifyRegist={() => {
                const param = wrap.data as LogicUtil.NodeInheritConnector;
                const isChange = param.method !== method.value || param.depend !== depend.value;
                param.method = method.value as LogicUtil.InheritMethod;
                param.depend = depend.value;
                param.id = identify.value;
                wrap.data = param;

                if (isChange) {
                    initConnectorNode(param);
                }
                invalidate();
            }}
        />
        <FormUtil.BorderPanel
            title="param"
            innerJsx={<>
                <FormUtil.FormRecord
                    titleLabel="Method"
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={method}
                            width={178}
                            isReadOnly={!isModify}
                            setCheckable={(checkable) => {
                                // createdepend
                                if (checkable.value === 'create') {
                                    setDepend({ ...depend, value: '' });
                                }
                                setMethod(checkable);
                            }}
                            list={LogicUtil.ConnectTypes.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            validates={[
                                {
                                    checker: (value) => ValidateUtil.checkRequired(value),
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Depend"
                    isEnable={method.value === 'depend'}
                    jsx={
                        <FormUtil.Combobox
                            marginLeft={2}
                            checkable={depend}
                            width={270}
                            isReadOnly={!isModify}
                            setCheckable={setDepend}
                            list={logicList.map(item => {
                                return { value: item, labelText: item };
                            })}
                            headBlank
                            dependency={[method]}
                            validates={[
                                {
                                    checker: (value) => {
                                        return method.value !== 'depend' || ValidateUtil.checkRequired(value);
                                    },
                                    errorType: "required"
                                }
                            ]}
                        />
                    }
                />
                <FormUtil.FormRecord
                    titleLabel="Identify"
                    jsx={
                        <FormUtil.TextField
                            marginLeft={2}
                            checkable={identify}
                            width={270}
                            isReadOnly={!isModify}
                            setCheckable={setIdentify}
                            validates={[
                                {
                                    checker: (value) => LogicUtil.checkFieldName(value),
                                    errorType: "value"
                                },
                                {
                                    checker: (value) => ValidateUtil.checkStringLength(value, 0, 20),
                                    errorType: "length"
                                },
                                {
                                    checker: (value) => {
                                        const list = idList.filter(id => {
                                            // id
                                            if (wrap.data != null) {
                                                return (wrap.data as LogicUtil.NodeInheritConnector).id !== id;
                                            }
                                            return true;
                                        });
                                        return !list.includes(value);
                                    },
                                    errorType: "relation"
                                }
                            ]}
                        />
                    }
                />
            </>}
        />
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<>
                <FormUtil.Record
                    textAlign="center"
                    jsx={<FormUtil.Switch
                        label={'Disable'}
                        width={300}
                        isUse={isDisable}
                        isEnable={!isModify}
                        callback={() => {
                            const data = (wrap.data as LogicUtil.NodeInheritConnector);
                            if (isDisable) {
                                data.disable = undefined;
                            } else {
                                data.disable = true;
                            }
                            invalidate();
                        }}
                    />}
                />
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Clone include',
                            isEnable: !isModify && !isCreate,
                            callback: () => {
                                store.system.dialog = <ConfirmDialog.Component
                                    message="Create a clone from the depended inherit logic."
                                    buttons={[
                                        {
                                            label: 'Cancel',
                                            callback: () => {
                                                store.system.dialog = null;
                                                setStore({ ...store });
                                            }
                                        },
                                        {
                                            label: 'Conversion',
                                            callback: () => {
                                                const logicMng = (rootData.data as LogicUtil.NodeRoot).logicMng.data as LogicUtil.NodeLogicManager;
                                                const logicList = logicMng.defs.map(def => def.data as LogicUtil.NodeLogicItem);
                                                // 
                                                const dependLogic = logicList.find(inherit => inherit.name === depend.value);
                                                if (dependLogic != undefined) {
                                                    const cloneLogic = JSON.parse(JSON.stringify(dependLogic)) as LogicUtil.NodeLogicItem;
                                                    const connectItem: LogicUtil.NodeInheritItem = {
                                                        mngs: cloneLogic.mngs
                                                    };
                                                    const newWrap: LogicUtil.WrapElement = {
                                                        type: 'inherit-logic',
                                                        data: connectItem
                                                    };
                                                    const newNode = LogicElementUtil.addChild(newWrap, node);
                                                    // 
                                                    newNode.children = LogicUtil.getChildrenElementNode(newNode);

                                                    const param = wrap.data as LogicUtil.NodeInheritConnector;
                                                    param.method = 'create';
                                                    param.depend = '';
                                                    mappingForm();
                                                }
                                                store.system.dialog = null;
                                                setStore({ ...store });
                                                invalidate();
                                            }
                                        }
                                    ]}
                                />;
                                setStore({ ...store });
                            }
                        }
                    ]}
                />
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Clone detach',
                            isEnable: !isModify && isCreate,
                            callback: () => {
                                store.system.dialog = <ConfirmDialog.Component
                                    message="Detach a clone from the this inherit logic."
                                    buttons={[
                                        {
                                            label: 'Cancel',
                                            callback: () => {
                                                store.system.dialog = null;
                                                setStore({ ...store });
                                            }
                                        },
                                        {
                                            label: 'Conversion',
                                            callback: () => {
                                                let rootNode = node;
                                                while (rootNode.parent != null) rootNode = rootNode.parent;
                                                const inheritNode = node.children.find(el => (el.data as LogicUtil.WrapElement).type === 'inherit-logic');
                                                const logicMngNode = rootNode.children.find(el => (el.data as LogicUtil.WrapElement).type === 'logic-mng');
                                                if (logicMngNode == undefined || inheritNode == undefined) return;
                                                const inherit = (inheritNode.data as LogicUtil.WrapElement).data;
                                                const clone = JSON.parse(JSON.stringify((inherit))) as LogicUtil.NodeInheritItem;
                                                // 
                                                const defaultName = LogicElementUtil.getNumberingOnPrefixName('clone_inherit_', logicMngNode);
                                                const logicItem: LogicUtil.NodeLogicItem = {
                                                    ...clone,
                                                    name: defaultName
                                                };
                                                const newWrap: LogicUtil.WrapElement = {
                                                    type: 'logic-item',
                                                    data: logicItem
                                                };

                                                // 
                                                const newNode = LogicElementUtil.addChild(newWrap, logicMngNode);
                                                // 
                                                newNode.children = LogicUtil.getChildrenElementNode(newNode);

                                                // 
                                                LogicElementUtil.removeChild(node, 2, 1);

                                                const param = wrap.data as LogicUtil.NodeInheritConnector;
                                                param.method = 'depend';
                                                param.depend = logicItem.name;
                                                mappingForm();

                                                store.system.dialog = null;
                                                setStore({ ...store });
                                                invalidate();
                                            }
                                        }
                                    ]}
                                />;
                                setStore({ ...store });
                            }
                        }
                    ]}
                />
                <FormUtil.ButtonRecord
                    isCenter
                    buttons={[
                        {
                            label: 'Delete',
                            isEnable: !isModify,
                            callback: () => {
                                removeSelf();
                            }
                        }
                    ]}
                />
                {/*  */}
                {LogicUtil.getSwapButtons(node, invalidate)}
            </>}
        />
    </>);
}

export default FactDetInheritConnector;